* {
    box-sizing: border-box;
}
@mixin df-c-c{
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
}
body {
    height: 100vh;
    max-width: 1600px;
    margin: 0 auto;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    background: #FFFEFC;
}

.header {
    padding: 40px;
    padding-bottom: 0px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;


    .logo {
        img {
            width: 110px;
        }

    }

    .header_dec {
        margin-top: -45px;

        img {
            width: 251px;
        }

    }

    .header_btn {
        display: flex;
        justify-content: space-around;
        width: 190px;

        img {
            width: 24px;
            height: 24px;
        }
    }

}

section {
    height: 100vh;
    padding: 0 40px;
    h2{
        font-size: 5vw;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-weight: 400;
        margin-bottom: 0;
        margin-top: 0;
    }
    p{
        margin: 0;
        
    }

}

.section-head {
    opacity: 0;
    transition: opacity 5s;
}

.section-head-scroll {
    opacity: 1;
}

.custom_h3 {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
margin-bottom: 0;
    color: #FF9401;
}

.custom_btn {
    margin-top: 20px;
    padding: 16px 33px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    text-transform: capitalize;
    color: #FFFFFF;
    background: #FF9401;
    border: #FF9401;
    border-radius: 31px;
    filter: drop-shadow(0px 20px 40px rgba(255, 148, 1, 0.2));
}
.main{
     background: #FFFEFC;
      padding: 0 40px;
      padding-top: 40px;
      padding-bottom: 40px;
      p{
        padding: 20px;
    }
    .main_img{
        margin-top: -80px;
        padding-bottom: 40px;
    }
}
.grid {
   
    // width: 100%;
    display: grid;
    grid-template-columns: 50% auto;   
    h1 {
        div {
            font-size: 60px;
            display: flex;
            align-items: center;

            .h1_img {
                display: inline-block;
                width: 60px;
                height: 60px;

            }
        }


    }

    .grid_img {

        img{
            width: 100%;
            height: auto;
            object-fit: cover;
        }
        &:last-child {
            img {
                width: 100%;
                height: auto;
                object-fit: contain;
            }
        }
    }
}
.about{
    background:  #FFFEFC linear-gradient(180deg, rgba(255, 148, 1, 0.05) 0%, rgba(255, 237, 211, 0) 100%);
    padding-top: 100px;
   
}
.about .grid{
    grid-template-columns:  auto 50%;
}
.features{
    
    background:#FFFEFC no-repeat center/100% url(/img/features/bg.png) ;
   padding-top: 120px;
    .features_desc{
       @include df-c-c();
    }
    .features_wrap{
        img {
                width: 50%;

            }
        width: 70%;
        
        display: flex;
        flex-wrap: wrap;
        align-items:baseline;
        justify-content: space-between;
        flex-grow: 1;
       
        .features_items{
           
            @include df-c-c();
            flex-grow: 1;
            max-width: 245px;
            min-width: 245px;
            margin: 0 auto;
           
           
        }
       
    }
    .custom_h3{
        padding-top: 20px;
    }
}
.recipes{
    background:  #FFFEFC linear-gradient(180deg, rgba(255, 148, 1, 0.05) 0%, rgba(255, 237, 211, 0) 100%);;
    padding-top: 30px;
    padding-bottom: 26px;
    .recipes_desc{
        @include df-c-c();
    }
    .recipes_head{
        display: flex;
        img{
            width: 86px;
        }
    }
    .recipes_wrap{
        width: 80%;
        display: flex;
        justify-content: space-between;
        padding-bottom: 72px;
        filter: drop-shadow(0px 40px 80px rgba(255, 148, 1, 0.15));
        
        img{
            width: 100%;
        }
        .recipes_items{
            background-color:#FFFFFF;
            max-width: 340px;
            padding: 5px;
            margin: 0 3%;

            border-radius:8px;
            @include df-c-c();
        }
    }
}
.takeaway{
    background:#FFFEFC no-repeat center/100% url(/img/takeaway/bg.png) ;
    padding-top: 100px;
    .takeaway_desc{
        width: 80%;
    }
    .app{
        display: flex;
        img{
            width: 150px;
        }
    }
}
.most_popular {
    width: 100%;
    height: 100vh;
    display: grid;
    place-items: center;
    place-content: center;
    position: relative;
    margin-top: 10vh;
    background:#FFFEFC;

    h2 {
        width: 100%;
        font-size: 5em;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        margin-top: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;

        span {
            display: inline-block;
        }
    }
}

.scewsec {
    transform: skew(15deg) rotate(6deg);
    
    span {
        display: inline-block;
        background: #FF9401;
        padding: 13px 100px;
        font-size: 3.5em;
        color:#FFFFFF;
        border-radius:8px;

    }

    &:last-child {
        transform: skew(-15deg) rotate(- 6deg);
        margin-top: 33vh;
    }
}

.subs {
    width: 100%;
    height: 100vh;
    display: grid;
    place-content: center;
    place-items: center;
    text-align: center;
    color:#FF9401;

    h2 {
        width: 100%;
        font-size: 6em;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

        span {
            display: inline-block;
        }
    }

}

.footer{ 
    background:#FFFEFC no-repeat center/100%  url(/img/footer/bg.png) ;
   padding: 0;
   padding-top: 80px;
   height: 378px;
   .footer_bg{
    
    background:   linear-gradient(180deg, rgba(255, 148, 1, 0.05) 0%, rgba(255, 237, 211, 0) 100%);
  
   }
  
    .footer_wrap{
        width: 70%;
         padding-top: 40px;
        display: flex;
        margin: 0 auto;
        justify-content: space-between;
    }

}
@media all and (max-width: 900px){
    .grid{
        grid-template-columns: auto;
    }
    .grid .grid_img:last-child img {
        width: 57%;
    }
    .main .main_img {
     margin-top: 0px; 
     padding-bottom: 0px;
       
    }
    .grid h1 div {
        font-size: 6vw;
    }
    .grid h1 div .h1_img {
        
        width: 6vw;
        height: 6vw;
    }
    .main p{
        padding: 20px 0;
    }
    .about .grid{
        grid-template-columns:  auto ;
    }
    .grid .grid_img img {
        width: 57%;
        
    }
    .features .features_wrap{
        width: 100%;
    }
    .features .features_wrap img {
        width: 20%;
    }
    .features .features_wrap .features_items{
        max-width: 100%;
        
    }
    .recipes .recipes_wrap {
        width: 100%;
    }
    .recipes .recipes_wrap .recipes_items{
        margin: 0 1%;
    }
    .recipes .recipes_head img {
        width: 10vw;
    }
    .scewsec span{
        font-size: 14px;
        padding: 12px 20px;
    }
    .most_popular h2{
        font-size: 1em;
    }
    .takeaway .app img {
        width: 90px;
    }

}
@media all and (max-width: 630px) {
    .header {
        .header_dec {
            img {
                display: none;
            }
        }
    }
}